import react from 'react'
import axios from 'axios'

class Index extends React.Component {
    constructor(props){
        super(props)
        
    }

    request(){
        axios.get("http://localhost/TEST/index.php")
        .then((res) => console.log(res.data))
    }
    getData(){
        axios.get("http://localhost/TEST/getdata.php", {
            params:{
                username:'Admin'
            }
        })
        .then( (resp) => console.log(resp))

    }
    postdata(){
        axios.post("http://localhost/TEST/post.php")

    }
    
    render(){
        return(
            <div>
                <button onClick={this.request.bind(this)} >REQUEST</button>
                <button onClick={this.request.bind(this)} > GET DATA </button>
                <button onClick={this.request.bind(this)} >POST AND RESPONSE</button>
            </div>
        )
    }
}

export default Index